import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '85%',
    margin: '7%',
    minHeight: '48vh'
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

export default function SimpleAccordion() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>¿Cómo quitar manchas de sangre?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <h5>Modo de uso</h5>
          <div  className="pasosConsejos"><ul>
          <li>Colocar debajo de la prenda manchada un trozo de algodón.</li>
          <li>Vierta el Quita Sangre sobre la mancha.</li>
          <li>Deje actuar unos minutos.</li>
          <li>Vierta nuevamente el Quita Sangre pero esta vez frotandola con el pico vertedor, hasta que la mancha desaparezca casi en su totalidad.</li>
          <li>Enjuague con abundante agua y jabón; y la mancha desaparecerá totalmente.</li>
          <li>En caso de ser necesario necesario, repetir los puntos 1 al 4.</li>
          <li>Lave la prenda normalmente.</li></ul></div>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography className={classes.heading}>¿Cómo quitar manchas de grasa?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <div className="pasosConsejos">
              <ul>
                <li>Antes de lavar la prenda, aplique Quita Grasa sobre la mancha frotando con el pico.</li>
                <li>Deje actuar unos minutos.</li>
                <li>Enjuague con abundante agua y jabón; y la mancha desaparecerá totalmente.</li>
                <li>En caso de ser necesario necesario, repetir los puntos 1 al 3.</li>
                <li>Lave la prenda normalmente.</li>
              </ul>
            </div>
        </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography className={classes.heading}>¿Cómo mantengo mi pileta?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
        <h5>MANTENIMIENTO DE PISCINAS</h5>

<p>¿Cómo calculamos el volumen de nuestra pileta? = LARGO X ANCHO X PROFUNIDAD</p>
<br/>
<h5>IMPORTANTE:</h5>
<p>Para posibilitar que cualquiera de los productos químicos funcione adecuadamente, es indispensable mantener el nivel de PH del Agua en el rango que va entre 7,2 mínimo y 7,6 máximo. Por eso debemos primero medir el PH, regularizarlo con PH (+) o PH(-) y recién después de esto debe aplicar los productos químicos. CLORO EN POLVO AL 60% (SHOCK O RAPIDO): 30 grs. cada 10.000 lts, todos los días.
Dosificar el doble después de un día de lluvia. Recomendado para piscinas pintadas o plásticas de PRFV.
CLORO AL 90 % LENTO: 20 grs. cada 10.000lts. Recomendado para piscinas revestidas. Combate el hongo negro.
Medición Práctica: Sugerimos la primera vez, colocar la medida total de cloro en un recipiente o vaso que no se use y hacer una marca en el nivel justo para su volumen de agua, en aplicaciones posteriores solo llene el recipiente con cloro hasta esa marca y lo aplica en la piscina. Ej. Si tiene una piscina de 40.000 litros coloque 80 grs. de cloro en el mismo y haga una marca para no tener que volver a medir en futuras ocasiones. 1 cucharada sopera colmada equivale a 15 grs. de polvo o granulado
CLORO LIQUIDO: Hipoclorito de sodio al 100% - 1 lt cada 20.000 lts de agua. Todos los días, dosificar el doble después de un día de lluvia. ALGUICIDA: 1 lt cada 100.000 lts de agua, una vez por semana. Duplicar la dosis después de un día de lluvia y se recomienda tirarlo la noche anterior a pasar el barrefondo.
CLARIFICANTE: 1 lt cada 100.000 lts de agua, una vez por semana o cuando el agua esté turbia, es indispensable controlar antes el PH y se recomienda tirarlo la noche anterior a pasar el barrefondo.
PASTILLAS TRIPLE ACCION x 200 GRS: 1 pastilla de 200 grs. cada 20.000 lts de agua por semana.</p>

<h5>CONSEJOS PRACTICOS:</h5>
<ul>
  <li>Filtrar la pileta al menos dos veces al día</li>
  <li>Colocar los productos por la tarde/noche y recircular el agua al colocarlos</li>
  <li>Para Combatir los pulgones de agua, agregar 500 gr de sal gruesa cada 50.000 litros de agua</li>
  <li>Si aparecen manchas ¿Cómo aceite en la superficie de la pileta lo más probable es que se haya colocado mayor cantidad de clarificante que lo recomendado. Para poder eliminarlas utilizar 5 litros de vinagre al alcohol cada 50.000 lt de agua</li>
  <li>Si el agua toma un color turbio se recomienda agregarle 5 lt de acido clorhídrico(muriático) cada 50.000 litros de agua.</li>
  <li>Pasar el barrefondo al menos una vez por semana </li>
</ul>
        </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}