import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import tileData from './TileData';
// import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
    width: '100%',
    marginTop: '3rem',
    minHeight: '48vh',
  },
  gridList: {
    width: '70%',
    height: '100vh',
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));

export default function TitlebarGridList() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      {/* <div className="category_list">
      <Button variant="outlined" color="primary" href="#">Lista completa</Button>
      <Button variant="outlined" color="primary" href="#limpieza">Lista limpieza</Button>
      <Button variant="outlined" color="primary" href="#plasticos">Lista plasticos</Button>
      <Button variant="outlined" color="primary" href="#pileta">Lista pileta</Button>
			</div> */}
      <GridList cellHeight={300}  spacing={30} className={classes.gridList}>
        <GridListTile key="Subheader" cols={4} style={{ height: 'auto' }}>
          <ListSubheader component="div"></ListSubheader>
        </GridListTile>
        {tileData.map((tile) => (
          <GridListTile key={tile.img}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              subtitle={<span>Precio Un.: {tile.preciox1}</span>}
              categoria={tile.categoria}
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
  );
}