import React, {Fragment, useState} from 'react'
import Button from '@material-ui/core/Button';
import { db } from '../firebase'

const FormContact = () =>{
    const [nombre, setNombre] = useState("");
    const [apellido, setApellido] = useState("");
    const [email, setEmail] = useState("");
    const [telefono, setTelefono] = useState("");
    const [message, setMessage] = useState("");
  
    const handleSubmit = (e) => {
      e.preventDefault();
  
      db.collection("contacts")
        .add({
          nombre: nombre,
          apellido: apellido,
          email: email,
          telefono: telefono,
          message: message,
        })
        .then(() => {
          alert("Your message has been submitted👍");
        })
        .catch((error) => {
          alert(error.message);
        });
  
      setNombre("");
      setApellido("");
      setEmail("");
      setTelefono("");
      setMessage("");
    };
    
    return(
        <Fragment>
        <h1>.</h1>
        <div className="titulo"><h1>Dejanos tu consulta llenando el formulario</h1></div>
        <div className="titulo"><h2>Nosotros nos comunicaremos a la brevedad</h2></div>
        <form className="row" onSubmit={handleSubmit}>
            <div className="doble">
                <input type="text" placeholder="Nombre" className="form-control" onChange={(e) => setNombre(e.target.value)} value={nombre}></input>
                <input type="text" placeholder="Apellido" className="form-control" onChange={(e) => setApellido(e.target.value)} value={apellido}></input>
            </div>
            <div className="doble">
                <input type="text" placeholder="E-mail" className="form-control" onChange={(e) => setEmail(e.target.value)} value={email}></input>
                <input type="text" placeholder="Numero de telefono" className="form-control" onChange={(e) => setTelefono(e.target.value)} value={telefono}></input> 
            </div>
            <textarea name="info" id="info" cols="50" rows="10" onChange={(e) => setMessage(e.target.value)} value={message} placeholder="Contanos tus consultas"></textarea>
            <br/>
            <div className="send"><Button type="submit" variant="outlined">Enviar</Button></div>
        </form>
        </Fragment>
    );
}

export default FormContact;